import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
export default {

	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			favoriteList: [],
			pageNo: 1,
			loadingType: 'more',
			isVip: false
		};
	},
	onShow() {
		this.isVip = this.$api.isVip()
	},
	onLoad(options) {
		this.loadData()
	},
	//下拉刷新
	onPullDownRefresh() {
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		this.loadData();
	},
	methods: {
		//获取收藏列表
		loadData(type) {
			const that = this
			if (type === 'refresh') {
				that.pageNo = 1
				that.favoriteList = []
				that.loadingType = 'more'
			}
			if (that.loadingType === 'more') {
				that.loadingType = 'loading'
				that.$api.request('collect', 'getCollectAll', {
					pageNo: that.pageNo
				}).then(res => {
					that.pageNo = res.data.pageNo + 1
					that.loadingType = res.data.pageNo < res.data.totalPageNo ? 'more' : 'nomore'
					res.data.items.forEach(item => {
						that.favoriteList.push(item);
						if (type === 'refresh') {
							uni.stopPullDownRefresh();
						}
					})
				})
			}
		},
		deleteFavorite(item) {
			const that = this
			that.$api.request('collect','deleteCollect', {
				spuId : item.spuId
			}).then(res => {
				that.loadData('refresh')
			})
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
	},
}
